'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-read-only-property' ;


// MAIN //

/**
* Top-level namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name exponentf
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/number/float32/base/exponent}
*/
import exponentf from '@stdlib/number-float32-base-exponent';
setReadOnly( ns, 'exponentf', exponentf );

/**
* @name fromBinaryStringf
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/number/float32/base/from-binary-string}
*/
import fromBinaryStringf from '@stdlib/number-float32-base-from-binary-string';
setReadOnly( ns, 'fromBinaryStringf', fromBinaryStringf );

/**
* @name fromWordf
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/number/float32/base/from-word}
*/
import fromWordf from '@stdlib/number-float32-base-from-word';
setReadOnly( ns, 'fromWordf', fromWordf );

/**
* @name normalizef
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/number/float32/base/normalize}
*/
import normalizef from '@stdlib/number-float32-base-normalize';
setReadOnly( ns, 'normalizef', normalizef );

/**
* @name signbitf
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/number/float32/base/signbit}
*/
import signbitf from '@stdlib/number-float32-base-signbit';
setReadOnly( ns, 'signbitf', signbitf );

/**
* @name significandf
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/number/float32/base/significand}
*/
import significandf from '@stdlib/number-float32-base-significand';
setReadOnly( ns, 'significandf', significandf );

/**
* @name toBinaryStringf
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/number/float32/base/to-binary-string}
*/
import toBinaryStringf from '@stdlib/number-float32-base-to-binary-string';
setReadOnly( ns, 'toBinaryStringf', toBinaryStringf );

/**
* @name float32ToInt32
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/number/float32/base/to-int32}
*/
import float32ToInt32 from '@stdlib/number-float32-base-to-int32';
setReadOnly( ns, 'float32ToInt32', float32ToInt32 );

/**
* @name float32ToUint32
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/number/float32/base/to-uint32}
*/
import float32ToUint32 from '@stdlib/number-float32-base-to-uint32';
setReadOnly( ns, 'float32ToUint32', float32ToUint32 );

/**
* @name toWordf
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/number/float32/base/to-word}
*/
import toWordf from '@stdlib/number-float32-base-to-word';
setReadOnly( ns, 'toWordf', toWordf );


// EXPORTS //

export default ns;
